<style type="text/css">
.header-wrap {
  width: 100%;
  height: 40px;

}
.msg-unread {
  color: #fff;
  position: absolute;
  z-index: 100;
  right: 10px;
  height: 40px;
  vertical-align: middle;
}
.msg-unread > span:nth-child(1) {
  line-height: 40px;
}
</style>
<template>
<div class="header-wrap">
  <el-header fixed :title="this.$route.name === 'bookdetail'? book_title : headerTitles[$route.name]">
    <a onclick="javascript:history.go(-1)" slot="left" v-show="!~['home', 'index', 'classShare', 'bookShare', 'explore'].indexOf($route.name)">
      <mt-button icon="back">返回</mt-button>
    </a>
    <a slot="right">
      <span>未读 </span><badge size="small" type="error">3</badge>
    </a>
  </el-header>
</div>
</template>

<script type="text/javascript">
import {Header ,Badge} from 'mint-ui';
export default {
  data() {
    return {
      headerTitles:{
        'home': '个人中心',
        'explore': '发现',
        'outbook': '发布书籍',
        'outclass': '发布课程',
        'userInfo': '个人信息',
        'signin': '登录',
        'signup': '注册',
        'classShare': '课程分享',
        'bookShare': '教材分享',
        'address': '我的配送地址',
        'myShareClass': '我分享的课程',
        'myShareBook': '我分享的教材',
        'myCollectClass': '我收藏的课程',
        'myCollectBook': '我收藏的书',
        'editaddress': '编辑地址',
        'myOrder': '我的订单',
        'pay': '付款',
        'mySellBook': '我卖出的书'
      },
      book_title: '',
      hasUnread: true,
    }
  },
  created() {
    BUS.$on('show_book_title', (val) => {
      this.$nextTick(() => {
        this.book_title = val;
      });
    });
    
  },
  components: {
    elHeader: Header,
    Badge
  }
}
</script>


