<style type="text/css">
.avatar {
  width: 80px;
}
.info-item-wrap {
  border-bottom: 10px solid #eee;
}
.popup-val {
  width: 300px;
  height: 300px;
  background-color: #fff;
  border-radius: 20px;
}
</style>
<template>
<div>
  <div class="info-item-wrap">
    <cell title="头像" icon="more">
      <img class="avatar" :src="userData.avatar">
    </cell>
    <cell @click.native="changeInfo('昵称')" title="昵称" is-link :value="userData.name"></cell>
    <cell @click.native="changeInfo('性别')" title="性别" is-link :value="gender[userData.gender]"></cell>
  </div>
  <div class="info-item-wrap">
    <cell @click.native="changeInfo('学校')" title="学校" is-link :value="userData.school"></cell>
    <cell @click.native="changeInfo('学院')" title="学院" is-link :value="userData.institute"></cell>
    <cell @click.native="changeInfo('专业')" title="专业" is-link :value="userData.subject"></cell>
    <cell @click.native="changeInfo('年级')" title="年级" is-link :value="userData.grade"></cell>
  </div>
  <div class="info-item-wrap">
    <cell to="/address" title="配送地址" is-link></cell>
  </div>
</div>
</template>
<script type="text/javascript">
import { Cell, Button, MessageBox, Toast } from 'mint-ui';
export default {
  data() {
    return {
      localUserData: this.userData,
      gender: ['男', '女', '未选择']
    }
  },
  props: ['userData'],
  components: {
    Cell, 'my-button': Button
  },
  methods: {
    changeInfo(type) {
      MessageBox.prompt(`请输入${type}`, '').then(({val}) => {
        if (typeof val === 'object' || val.trim() === '') {
          Toast({
            message: '请检查您的输入是否合法',
            duration: 1000
          });
          return;
        }
        this.$http.post(IP + ':2017/changeInfo', {type: type, value: val}).then(({data})=> {
          BUS.$emit('user', data);
        })
      }).catch((val) => {
        console.log(val);
      });
    },
  }
}
</script>


