<style type="text/css">
.classItem-wrap {
  width: 100%;
  border-bottom: 1px solid #eee;
  padding: 10px;
  box-sizing: border-box;
  position: relative;
}
.add-collect, 
.edit-class {
  position: absolute;
  right: 10px;
  bottom: 56px;
}
.dele-class {
  position: absolute;
  right: 10px;
  bottom: 8px;
}
.classItem > h3 {
  font-size: 26px;
  margin-bottom: 4px;
  font-weight: normal;
}
.classItem > p {
  margin-bottom: 2px;
  color: #545050;
}

</style>
<template>
<div class="classItem-wrap" @click="test">
  <div class="classItem">    
    <h3>{{classItem.name}}</h3>
    <p>类型：{{classItem.type}}</p>
    <p>学校：{{classItem.school}} <span>教室：{{classItem.classroom}}</span></p>
    <p>日期：{{classItem.date}}</p>
    <p>时间：{{classItem.upTime}}--{{classItem.downTime}}</p>
  </div>
  <my-button 
    v-if="($route.name==='classShare' 
      && !!~userData.collectClass.indexOf(classItem._id))
    ||hasCollect" 
    disabled 
    class="add-collect" 
    type="primary" 
    size="normal">已收藏</my-button>
  <my-button v-else v-show="$route.name==='classShare'" @click.stop="addToCollection" class="add-collect" type="primary" size="normal">+加入收藏</my-button>
  <my-button v-if="$route.name==='myCollectClass'" @click.stop="removeToCollection" class="add-collect" type="primary" size="normal">取消收藏</my-button>
  <my-button  v-if="$route.name==='myShareClass'" @click.stop="editClass" class="edit-class" type="primary" size="normal">编辑</my-button>
  <my-button  v-if="$route.name==='myShareClass'" @click.stop="showDeleButton" class="dele-class" type="primary" size="normal">删除</my-button>
</div>
</template>
<script type="text/javascript">
import { Button, Toast, MessageBox} from 'mint-ui';
export default {
  data() {
    return {
      hasCollect: false,
    }
  },
  components: {'my-button': Button, MessageBox},
  props: ['classItem', 'userData'],
  methods: {
    test() {
    },
    addToCollection() {
      if (!this.userData._id) {
        Toast({
          message: '还未登录，正在跳转到登录页面',
          duration: 1500
        });
        setTimeout( () => {
          location.href= IP + ":8090/#/signin";
        }, 1500);

        return;
      }

      this.$http.post(IP + ':2017/addClassCollect', {classID: this.classItem._id, userID: this.userData._id}).then(({data}) => {
          Toast({
            message: '收藏成功',
            duration: 1000
          });
          this.hasCollect = true;
          BUS.$emit('user', data);
      });
    },
    removeToCollection() {

    },
    editClass() {

    },
    showDeleButton() {
      MessageBox.confirm('确定删除该课程?').then( () => {
        this.deleClass();
      }).catch(() => {
        return ;
      } );
    },
    deleClass() {
      this.$http.post(IP + ':2017/deleClass', {classID: this.classItem._id}).then(({data}) => {
        if (data==='success') {
          Toast({
            message: '删除成功',
            duration: 1000
          });
          BUS.$emit('updateClass');
        }
      });
    }
  },
}
</script>


